import vButton from '../../components/button'
import vModal from '../../components/modal'
import message from '../../components/message'

const confirm = vModal.confirm
const info = vModal.info
const success = vModal.success
const error = vModal.error

export default {
  data () {
    return {
      modalText: '对话框的内容',
      confirmLoading: false,
      visible: false,
      visible2: false
    }
  },

  components: { vModal, vButton },

  methods: {
    _showModal () {
      this.visible = true
    },

    _handleOk () {
      message.success('点击了确定')
      this.confirmLoading = false
      this.visible = false
    },

    _handleCancel () {
      message.info('点击了取消')
      this.visible = false
    },

    _showModal2 () {
      this.visible2 = true
    },

    _handleOk2 () {
      message.success('点击了确定')
      this.modalText = '对话框将在两秒后关闭'
      this.confirmLoading = true
      setTimeout(() => {
        this.visible2 = false
        this.confirmLoading = false
      }, 2000)
    },

    _handleCancel2 () {
      message.info('点击了取消')
      this.visible2 = false
    },

    _showConfirm () {
      confirm({
        title: '您是否确认要删除这项内容',
        content: '一些解释',
        onOk: function() {
          console.log('确定')
        },
        onCancel: function() {}
      })
    },

    _showConfirm2 () {
      confirm({
        title: '您是否确认要删除这项内容',
        content: '点确认 1 秒后关闭',
        onOk: function() {
          return new Promise(function(resolve) {
            setTimeout(resolve, 1000)
          })
        },
        onCancel: function() {}
      })
    },

    info() {
      info({
        title: '这是一条通知信息',
        content: '一些附加信息一些附加信息一些附加信息',
        onOk: function() {}
      })
    },

    success() {
      success({
        title: '这是一条通知信息',
        content: '一些附加信息一些附加信息一些附加信息'
      })
    },

    error() {
      error({
        title: '这是一条通知信息',
        content: '一些附加信息一些附加信息一些附加信息'
      })
    }
  }
}


