import { defaultProps } from '../../utils'

export default {
  props: defaultProps({
    prefixCls: 'ant-timeline',
    color: 'blue',
    timelineLast: false,
    pending: false
  }),

  computed: {
    endCls () {
      return this.pending && this.timelineLast ? this.prefixCls + '-item-last' : ''
    },
    lastTailShow () {
      return this.timelineLast && !this.pending ? 'none' : 'block'
    }
  },

  methods: {

  }
}


