module.exports = "<div class=markdown><h1 id=tag>Tag</h1><hr><p>进行标记和分类的小标签。</p><h2 id=->何时使用</h2><ul><li>用于标记事物的属性和维度。</li><li>进行分类。</li></ul><h2 id=->组件演示</h2><div class=code-boxes><div class=\"code-box code-boxes-col\"><h3>基本</h3><div class=\"code-boxes-col-2-1 code-box-demo\"><v-tag>标签一</v-tag><v-tag>标签二</v-tag><v-tag closable :on-close=_onClose>标签三</v-tag><v-tag href=http://www.baidu.com>标签四 (链接)</v-tag></div><div class=\"code-boxes-col-2-1 code-box-code\"><pre>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-tag</span><span class=\"token punctuation\">></span></span>标签一<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-tag</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-tag</span><span class=\"token punctuation\">></span></span>标签二<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-tag</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-tag</span> <span class=\"token attr-name\">closable</span> <span class=\"token attr-name\">:on-close</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>_onClose<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>标签三<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-tag</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-tag</span> <span class=\"token attr-name\">href</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>http://www.baidu.com<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>标签四 (链接)<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-tag</span><span class=\"token punctuation\">></span></span>\n</pre></div></div><div class=\"code-box code-boxes-col\"><h3>四种颜色的类型</h3><div class=\"code-boxes-col-2-1 code-box-demo\"><v-tag closable color=blue>蓝色</v-tag><v-tag closable color=green>绿色</v-tag><v-tag closable color=yellow>黄色</v-tag><v-tag closable color=red>红色</v-tag></div><div class=\"code-boxes-col-2-1 code-box-code\"><pre>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-tag</span> <span class=\"token attr-name\">closable</span> <span class=\"token attr-name\">color</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>blue<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>蓝色<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-tag</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-tag</span> <span class=\"token attr-name\">closable</span> <span class=\"token attr-name\">color</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>green<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>绿色<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-tag</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-tag</span> <span class=\"token attr-name\">closable</span> <span class=\"token attr-name\">color</span><span class=\"token at