module.exports = "<div class=markdown _v-61579404=\"\"><h1 id=layout- _v-61579404=\"\">Layout 布局</h1><hr _v-61579404=\"\"><p _v-61579404=\"\">24 栅格系统。</p><h2 id=- _v-61579404=\"\">设计理念</h2><div class=\"row demo-row\" _v-61579404=\"\"><div class=\"col-24 demo-col demo-col-1\" _v-61579404=\"\">100%</div></div><div class=\"row demo-row\" _v-61579404=\"\"><div class=\"col-6 demo-col demo-col-2\" _v-61579404=\"\">25%</div><div class=\"col-6 demo-col demo-col-3\" _v-61579404=\"\">25%</div><div class=\"col-6 demo-col demo-col-2\" _v-61579404=\"\">25%</div><div class=\"col-6 demo-col demo-col-3\" _v-61579404=\"\">25%</div></div><div class=\"row demo-row\" _v-61579404=\"\"><div class=\"col-8 demo-col demo-col-4\" _v-61579404=\"\">33.33%</div><div class=\"col-8 demo-col demo-col-5\" _v-61579404=\"\">33.33%</div><div class=\"col-8 demo-col demo-col-4\" _v-61579404=\"\">33.33%</div></div><div class=\"row demo-row\" _v-61579404=\"\"><div class=\"col-12 demo-col demo-col-1\" _v-61579404=\"\">50%</div><div class=\"col-12 demo-col demo-col-3\" _v-61579404=\"\">50%</div></div><div class=\"row demo-row\" _v-61579404=\"\"><div class=\"col-16 demo-col demo-col-4\" _v-61579404=\"\">66.66%</div><div class=\"col-8 demo-col demo-col-5\" _v-61579404=\"\">33.33%</div></div><p _v-61579404=\"\">在多数业务情况下，Ant Design需要在设计区域内解决大量信息收纳的问题，因此在12栅格系统的基础上，我们将整个设计建议区域按照24等分的原则进行划分。</p><p _v-61579404=\"\">划分之后的信息区块我们称之为“盒子”。建议横向排列的盒子数量最多四个，最少一个。“盒子”在整个屏幕上占比见上图。设计部分基于盒子的单位定制盒子内部的排版规则，以保证视觉层面的舒适感。</p><h2 id=- _v-61579404=\"\">概述</h2><p _v-61579404=\"\">布局的栅格化系统，我们是基于行（row）和列（col）来定义信息区块的外部框架，以保证页面的每个区域能够稳健地排布起来。下面简单介绍一下它的工作原理：</p><ul _v-61579404=\"\"><li _v-61579404=\"\">通过<code _v-61579404=\"\">row</code>在水平方向建立一组<code _v-61579404=\"\">column</code>（简写col）</li><li _v-61579404=\"\">你的内容应当放置于<code _v-61579404=\"\">col</code>内，并且，只有<code _v-61579404=\"\">col</code>可以作为<code _v-61579404=\"\">row</code>的直接元素</li><li _v-61579404=\"\">栅格系统中的列是指1到24的值来表示其跨越的范围。例如，三个等宽d的列可以使用<code _v-61579404=\"\">.col-8</code>来创建</li><li _v-61579404=\"\">如果一个<code _v-61579404=\"\">row</code>中的<code _v-61579404=\"\">col</code>总和超过24，那么多余的<code _v-61579404=\"\">col</code>会作为一个整体另起一行排列</li></ul><h2 id=flex- _v-61579404=\"\">Flex 布局</h2><p _v-61579404=\"\">我们的栅格化系统支持 Flex 布局，允许子元素在父节点内的水平对齐方式 - 居左、居中、居右、等宽排列、分散排列。子元素与子元素之间，支持顶部对齐、垂直居中对齐、底部对齐的方式。同时，支持使用 order 来定义元素的排列顺序。</p><p _v-61579404=\"\">Flex 布局是基于 24 栅格来定义每一个“盒子”的宽度，但排版则不拘泥于栅格。</p><h2 id=- _v-61579404=\"\">组件演示</h2><div class=code-boxes _v-61579404=\"\"><div class=\"code-box code-boxes-col\" _v-61579404=\"\"><h3 _v-61579404=\"\">基础布局</h3><div class=\"code-boxes-col-2-1 code-box-demo\" _v-61579404=\"\"><v-row _v-61579404=\"\"><v-col span=12 _v-61579404=\"\">.col-12</v-col><v-col span=12 _v-61579404=\"\">.col-12</v-col></v-row><v-row class=testRowClassName _v-61579404=\"\"><v-col span=8 _v-61579404=\"\">.col-8</v-col><v-col span=8 _v-61579404=\"\">.col-8</v-col><v-col span=8 class=testColClassName _v-61579404=\"\">.col-8</v-col></v-row><v-row _v-61579404=\"\"><v-col span=6 _v-61579404=\"\">.col-6</v-col><v-col span=6 _v-61579404=\"\">.col-6</v-col><v-col span=6 _v-61579404=\"\">.col-6</v-col><v-col span=6 _v-61579404=\"\">.col-6</v-col></v-row></div><div class=\"code-boxes-col-2-1 code-box-code\" _v-61579404=\"\"><pre _v-61579404=\"\"><span class=\"token tag\" _v-61579404=\"\"><span class=\"token tag\" _v-61579404=\"\"><span class=\"token punctuation\" _v-61579404=\"\">&lt;</span>v-row</span><span class=\"token punctuation\" _v-61579404=\"\">&gt;</span></span>\n  <span class=\"token tag\" _v-61579404=\"\"><span class=\"token tag\" _v-61579404=\"\"><span class=\"token punctuation\" _v-61579404=\"\">&lt;</span>v-col</span> <span class=\"token attr-name\" _v-61579404=\"\">span</span><span class=\"token attr-value\" _v-61579404=\"\"><span class=\"token punctuation\" _v-61579404=\"\">=</span><span class=\"token punctuation\" _v-61579404=\"\">\"</span>12<span class=\"token punctuation\" _v-61579404=\"\">\"</span></span><span class=\"token punctua