module.exports = "<div class=markdown><h1 id=table>Table</h1><hr><p>展示行列数据。</p><h2 id=->何时使用</h2><ul><li>当有大量结构化的数据需要展现时；</li><li>当需要对数据进行排序、搜索、分页、自定义操作等复杂行为时。</li></ul><h2 id=->如何使用</h2><p>Table 有两种模式，本地数据和远程数据模式。</p><p><strong>本地数据模式</strong>是指数据一次性载入内存，纯前端进行分页、筛选、排序等功能。</p><p>通过指定表格的数据源 <code>dataSource</code> 为一个数据数组。</p><p><strong>远程数据模式</strong>是更常见的业务场景，是一次只从服务端读取一页的数据放在前端，执行筛选、排序、切换页码等操作时均向后台发送请求，后台返回当页的数据和相关分页信息。</p><p>通过指定表格的数据源 <code>dataSource</code> 为一个 DataSource 的实例如下。</p><h2 id=->组件演示</h2><div class=code-boxes><div class=\"code-box code-boxes-col\"><h3>简单实用</h3><div class=\"code-boxes-col-2-1 code-box-demo\"><v-table :columns=columns :data-source=dataSource></v-table></div><div class=\"code-boxes-col-2-1 code-box-code\"><pre>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-table</span> <span class=\"token attr-name\">:columns</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>columns<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">:data-source</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>dataSource<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-table</span><span class=\"token punctuation\">></span></span>\n</pre></div></div></div><h2 id=api>API</h2><h3 id=table>Table</h3><table><thead><tr><th>参数<th>说明<th>类型<th>可选值<th>默认值<tbody><tr><td>rowSelection<td>列表项是否可选择<td>Object<td><td>false<tr><td>pagination<td>分页器<td>Object<td>配置项参考 <a href=/components/pagination>pagination</a>，设为 false 时不显示分页<td><tr><td>size<td>正常或迷你类型<td>String<td><code>normal</code> or <code>small</code><td>normal<tr><td>dataSource<td>数据源，可以为数组（本地模式）或一个数据源描述对象（远程模式）<td>Array or Object<td><td><tr><td>columns<td>表格列的配置描述，具体项见下表<td>Array<td><td>无<tr><td>rowKey<td>表格列 key 的取值<td>Function(recode, index):string<td><td>record.key<tr><td>expandIconAsCell<td>设置展开 Icon 是否单独一列<td>Boolean<td><td>true<tr><td>onChange<td>分页、排序、筛选变化时触发<td>Function(pagination, filters, sorter)<td></table><h3 id=column>Column</h3><p>列描述数据对象，是 columns 中的一项。</p><table><thead><tr><th>参数<th>说明<th>类型<th>可选值<th>默认值<tbody><tr><td>title<td>列头显示文字<td>String or React.Element<td><td><tr><td>dataIndex<td>列数据在 data 中对应的 key<td>String<td><td><tr><td>key<td>React 需要的 key<td>String<td><td><tr><td>render<td>生成复杂数据的渲染函数，参数分别为当前列的值，当前列数据，列索引<td>Function(text, record, index) {}<td><td><tr><td>filters<td>表头的筛选菜单项<td>Array<td><td><tr><td>onFilter<td>本地模式下，确定筛选的运行函数<td>Function<td><td><tr><td>sorter<td>排序函数，本地模式下为一个函数，远程模式下为布尔值<td>Function or Boolean<td><td>无<tr><td>width<td>列宽度<td>String or Number<td><td>无</table><h3 id=datasource>dataSource</h3><p>远程数据源配置对象。</p><table><thead><tr><th>参数<th>说明<th>类型<th>可选值<th>默认值<tbody><tr><td>url<td>数据源地址<td>String<td><td><tr><td>resolve<td>获得数据的解析函数，接收参数为远程数据返回的 result<td>Function<td><td><tr><td>getPagination<td>和后台接口返回的分页数据进行适配的函数，返回值会传给表格中的分页器<td>Function<td><td><tr><td>getParams<td>和后台接口接收的参数进行适配，返回值会作为请求的参数发送<td>Function<td><td>无</table></div>";


