module.exports = "<div class=markdown _v-a8c514bc=\"\"><h1 id=iconfont _v-a8c514bc=\"\">Iconfont</h1><hr _v-a8c514bc=\"\"><p _v-a8c514bc=\"\">有含义的矢量图形，每一个图标打倒一个敌人。</p><h2 id=- _v-a8c514bc=\"\">图标的命名规范</h2><p _v-a8c514bc=\"\">我们为每个图标赋予了语义化的命名。只需在 <code _v-a8c514bc=\"\">&lt;v-icon&gt;&lt;/v-icon&gt;</code> 标签内，制定对应的 type 属性即可。</p><p _v-a8c514bc=\"\">不同 type 命名规则如下:</p><ul _v-a8c514bc=\"\"><li _v-a8c514bc=\"\"><p _v-a8c514bc=\"\">实心和描线图标保持同名，用 <code _v-a8c514bc=\"\">-o</code> 来区分，比如 <code _v-a8c514bc=\"\">question-circle</code>(实心) 和 <code _v-a8c514bc=\"\">question-circle-o</code>(描线)；</p></li><li _v-a8c514bc=\"\"><p _v-a8c514bc=\"\">命名顺序：<code _v-a8c514bc=\"\">[icon名]-[描线与否]-[方向]</code>。</p></li></ul><h2 id=- _v-a8c514bc=\"\">实现原理</h2><p _v-a8c514bc=\"\">所有的 Icon 标签最终会渲染为</p><pre _v-a8c514bc=\"\"><code class=lang-html _v-a8c514bc=\"\">&lt;i class=\"anticon anticon-${type}\"&gt;&lt;/i&gt;\n</code></pre><h2 id=- _v-a8c514bc=\"\">如何使用</h2><p _v-a8c514bc=\"\">使用 <code _v-a8c514bc=\"\">&lt;v-icon&gt;</code> 标签申明组件，设置对应的 type 设置需要渲染的图标，示例代码如下:</p><pre _v-a8c514bc=\"\"><code class=lang-html _v-a8c514bc=\"\">&lt;v-icon type=\"link\" &gt;&lt;/v-icon&gt;\n</code></pre><blockquote _v-a8c514bc=\"\"><p _v-a8c514bc=\"\">点击图标复制代码。</p></blockquote><h2 id=- _v-a8c514bc=\"\">图标列表</h2><h3 id=- _v-a8c514bc=\"\">一. 方向性图标</h3><ul class=\"anticons-list clearfix\" _v-a8c514bc=\"\"><li v-for=\"item in icons1Obj\" :data-clipboard-text=_getCopyCode(item.name) :class=\"{copied: item.justCopied}\" _v-a8c514bc=\"\"><v-icon :type=item.name _v-a8c514bc=\"\"></v-icon><span class=anticon-class _v-a8c514bc=\"\">{{item.name}}</span></li></ul><h3 id=- _v-a8c514bc=\"\">二. 提示建议性图标</h3><ul class=\"anticons-list clearfix\" _v-a8c514bc=\"\"><li v-for=\"item in icons2Obj\" :data-clipboard-text=_getCopyCode(item.name) :class=\"{copied: item.justCopied}\" _v-a8c514bc=\"\"><v-icon :type=item.name _v-a8c514bc=\"\"></v-icon><span class=anticon-class _v-a8c514bc=\"\">{{item.name}}</span></li></ul><h3 id=- _v-a8c514b